<template>
<modal modalWidth="18em">
  <template #content>
    <div class="modal-header">Результат</div>
    <div class="chart-wrapper relative">
      <chart-doughnut :data="[percentage, 100-percentage]" :colors="[mix3(percentage), '#00000015']" :thickness='5'>
        <div class="chart-percentage">{{ percentage }}%</div>
        <div class="chart-label">правильно</div>
      </chart-doughnut>
      <chart-doughnut v-if='percentageAvg' class='chart-inset centered' 
       :data="[percentageAvg, 100-percentageAvg]" 
       :colors="[mix3(percentageAvg), '#00000015']" 
       :thickness='2'/>
    </div>
    <br>
    <div class="modal-text" v-if='percentageAvg'>
      {{ percentage > percentageAvg ? 'Оце так!' : percentage == percentageAvg ? 'Чудово.' : 'Ви були близько.' }} Ваш результат 
      <span :style='{ color: percentage >= percentageAvg ? mix3(100) : mix3(percentage) }' style="font-weight: bold">
        {{ percentage > percentageAvg ? `на ${percentage - percentageAvg}% вище за` : percentage == percentageAvg ? 'такий саме, як' : "дещо нижче за" }}
      </span>
      середній
    </div>
  </template>
  <template #buttons></template>
</modal>
</template>

<script>
import chartDoughnut from '../charts/chart-doughnut.vue'
import modal from '@/components/modals/modal-basic.vue'
export default {
  components: { modal, chartDoughnut },
  props: {
    percentage: { type: Number, required: true },
    percentageAvg: { type: Number, required: false, default: undefined },
  }
}
</script>

<style lang="scss" scoped>
.chart-inset {
  width: 10.5em;
  height: 10.5em;
}
</style>