<template>
  <paginationItem v-for="(label,i) in pageMax" :key='i' 
    v-show='pageRange.includes(i)'
    ref='paginationItems'
    @click='$emit("update:page", i)'
    :label='label' :wings='[i != 0, i != pageMax-1]' 
    :color='colors[i] ?? "gray"' 
    :shape='"square"' 
    :width='100/(Math.min(pageMax, pageLimit + 1)) + "%"'/>
</template>

<script>
import paginationItem from '@/components/pagination-item.vue'
export default {
  components: { paginationItem },
  props: {
    page: { type: Number, required: true, default: 0 },
    pageMax: { type: Number, required: true, default: 0 },
    pageLimit: { type: Number, required: true, default: 1 },
    colors: { type: Array, required: false, default: [] }
  },
  mounted() { this.$refs.paginationItems[0].click() },
  computed: {
    pageRange() {
      let { page, pageLimit, pageMax } = this
      let pages = [ ...Array(pageMax).keys() ]
      if (pageMax <= pageLimit) return pages 
      else {
        let n = (pageLimit + 1)/2
        if (page < n) { return pages.slice(0, pageLimit + 1) }
        else if (page > pageMax - n - 1) { return pages.slice(pageMax - pageLimit - 1, pageMax) }
        else { return pages.filter(i => Math.abs(i - page) < n) }
      }
    }
  },
  methods: {
    pageGoto(val) { this.$refs.paginationItems[val].click() }
  }
}
</script>